<template>
    <section class="header default-section" id="navhead" @click="photoClick">
      <div class="container">
        <div class="row text-center">
          <div class="col-md-5 inline-div">
            <h1 class="header-title">
                <a class="photo-icon">
                  <i class="uf uf-camera"></i>
                  <span class="popup-photo" :style="{opacity:opacity}">
                    <span class="shadow-popup"></span>
                    <span class="popup-place">
                      <i class="btn-close uf uf-close-small"></i>
                      <img :src="data.photo" 
                        @mouseover="photoToggle"
                        @mouseout="photoToggle">
                      <span class="popup-text">{{data.greet}}</span>
                    </span>
                  </span>
                </a>
                {{data.h1}}

            </h1>
          </div>
        </div>
        <div class="row menu">
          <div class="col-md-12">
            <div class="mob-menu">
              <ul>
                <slot name="menu_icon">Here is icon menu.</slot>
              </ul>
            </div>
          </div>
        </div>
        <!-- <div class="row socials">
          <div class="col-md-12">
            <a>facebook</a>
            <a href=""> twitter</a>
            <a href="">google+</a>
            <a href="">dribbble</a>
            <a href="">instagram</a>
          </div>
        </div> -->
      </div>
    </section>
</template>

<script>
export default {
  name: 'Myheader',
  data () {
    return {
      opacity: 0
    }
  },
  props: ['data'],
  methods: {
    photoClick (e) {
      let hasNoClass = (...names) => {
        let b = true
        names.forEach(name => {
          b = b && !e.target.parentElement.classList.contains(name)
        })
        return b
      }
      if (hasNoClass('popup-place', 'popup-photo', 'photo-icon') || e.target.classList.contains('btn-close')) {
        this.opacity = 0
      }
      if (e.target.className === 'uf uf-camera' || e.target.className === 'photo-icon') {
        this.opacity = 1 - this.opacity
      }
    },
    photoToggle (e) {
      let arr = ['_0.jpg', '_1.jpg']
      let suffix = e.target.src.endsWith(arr[0]) ? arr : arr.reverse()
      e.target.src = e.target.src.split(suffix[0])[0] + suffix[1]
    }
  }
}
</script>



