<script>
import myData from '@/assets/js/data'

function syntaxHighlight (json) {
  json = JSON.stringify(json, undefined, 2)
  return json.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;')
    .replace(/},\n\s+{/g, '}, {')
    .replace(/(\n\s{2}"\w+":\s{\n)|(\n\s{2}}(,|\n))/g, m => {
      let key = m.replace(/\W/g, '')
      return /{\n$/.test(m)
        ? '\n  <router-link to=\'/graphic/' + key + '\'>' +
          '<span class=\'glyphicon glyphicon-link\'/>&quot;' + key + '&quot;' +
          '</router-link>: {' +
          '<div class=\'group\'>'
        : '</div>' + m.replace(/\n/, '')
    })
    .replace(/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+-]?\d+)?)/g, function (match) {
      var cls = 'number'
      if (/^"/.test(match)) {
        if (/:$/.test(match)) {
          cls = 'key'
        } else {
          cls = 'string'
        }
      } else if (/true|false/.test(match)) {
        cls = 'boolean'
      } else if (/null/.test(match)) {
        cls = 'null'
      }
      return '<span class="' + cls + '">' + match + '</span>'
    })
}
export default {
  name: 'StyleText',
  template: '<pre class="style-text">' + syntaxHighlight(myData) + '</pre>'
}
</script>

<style lang="stylus">
pre
  padding: 0 10%
  margin: 0
  border-radius: 0
  white-space: pre-wrap
  border: none
  background-color: #333
  color: lightgray
  font-size: 16px
  @media (max-width: 759px)
    padding: 0 10px
  .key
    color: khaki
  .string
    color: cyan
  .number
    color: lightgreen
  .boolean
    color: blue
  .group
    border-radius: 4px
    &:hover
      background-color: #3f3f3f
  a
    position: relative
    color: #aaa
    font-weight: 600
    &:hover
      text-decoration: underline
    .glyphicon
      position: absolute
      margin-left: -18px
</style>



