<template>
    <!--Start section Resume -->
    <section id="navresume" class="resume-section default-section">
      <!-- Start container-->
      <div class="container">
        <!-- Start row-->
        <div class="row">
          <div class="col-md-12">
            <img src="static/img/resume.svg" class="section-img" alt="">
            <h2 class="section-title">{{title}}</h2>
          </div>
        </div>
        <!-- End row-->
        <!-- Start row-->
        <div class="row skills-block">
          <h3 class="text-center">{{data.skills.title}}</h3>
          <div class="col-md-4"
            v-for="col in 2"
            :class="col===1?'col-md-offset-2':''">
            <!-- Start Progress-wrapper -->
            <div class="progress-wrapper skillbar" 
              v-for="(item, index) in skills"
              v-if="index>=(col-1)*skills.length/2 && index<col*skills.length/2">
              <label class="progress-bar-label">{{item.name}}</label>
              <span class="progress-bar-number">{{item.disp}}</span>
              <!-- Start Progress -->
              <div class="progress">
                <div class="progress-bar" :style="{width:item.disp}"></div>
              </div>
              <!-- End Progress -->
            </div>
            <!-- End Progress-wrapper -->
          </div>
        </div>
        <!-- End row-->
        <!-- Start row-->
        <div class="row">
          <div class="col-md-4 col-md-offset-2">
            <img src="static/img/devices.svg" alt="">
          </div>
          <div class="col-md-4">
            <h3>{{data.service.title}}</h3>
            <!-- Start ul -->
            <ul class="resume-list">
              <li v-for="item in data.service.data">{{item}}</li>
            </ul>
            <!-- End ul -->
          </div>
        </div>
        <!-- End row-->
      </div>
      <!-- End container-->
    </section>
    <!--End section  resume -->
</template>

<script>
function dataInit (vm) {
  let skillsData = JSON.stringify(vm.$props.data.skills.data)
  vm.skills = JSON.parse(skillsData).map(item => {
    item.disp = '0%'
    return item
  })
}

function dataAnimation (vm) {
  vm.skills.forEach(item => {
    let start = null
    let duration = 1000
    let step = timestamp => {
      if (timestamp === undefined) {
        timestamp = performance.now()
        start = timestamp
      }
      let deltaTime = timestamp - start
      let elapsed = deltaTime / duration
      elapsed = elapsed > 1 ? 1 : elapsed
      elapsed = 0.5 * (1 - Math.cos(Math.PI * elapsed))
      item.disp = (elapsed * item.percent).toFixed() + '%'
      deltaTime <= duration && requestAnimationFrame(step)
    }
    step()
  })
}
export default {
  name: 'Resume',
  data () {
    return {
      skills: [],
      title: this.$options._componentTag
    }
  },
  props: ['data', 'appear'],
  mounted () {
    dataInit(this)
  },
  watch: {
    '$props.appear' (to, from) {
      if (to === 2) {
        dataAnimation(this)
      }
    }
  }
}
</script>



