<template>
  <div id="pano"></div>
</template>

<script>
function addClass (cls, ...nodes) {
  nodes.forEach(node => {
    let arr = node.className.split(' ')
    if (!arr.includes(cls)) {
      arr.push(cls)
    }
    node.className = arr.join(' ')
  })
}

function removeClass (cls, ...nodes) {
  nodes.forEach(node => {
    let arr = node.className.split(' ')
    node.className = arr.filter(item => item !== cls).join(' ')
  })
}

export default {
  name: 'VR',
  mounted () {
    addClass('full-screen', document.body, document.documentElement, document.querySelector('#pano'))
    window.embedpano({
      xml: 'static/vr/IndexScene.xml',
      target: 'pano',
      html5: 'webgl',
      mobilescale: 1.0,
      consolelog: true
    })
  },
  destoryed () {
    removeClass('full-screen', document.body, document.documentElement)
  }
}
</script>

<style>
  .full-screen{
    width: 100%;
    height: 100%;
  }
</style>



