<template>
    <!--Start section  About -->
    <section id="navabout" class="about-section default-section">
      <!-- Start container-->
      <div class="container">
        <!-- Start row-->
        <div class="row">
          <div class="col-md-12">
            <img src="static/img/about.svg" class="section-img" alt="">
            <h2 class="section-title">{{title}}</h2>
          </div>
        </div>
        <!-- End row-->
        <!-- Start row-->
        <div class="row">
          <div class="col-md-4 col-md-offset-2">
            <h3>{{data.myInformation.title}}</h3>
            <!-- Start about-info-->
            <p class="about-info" 
              v-for="item in data.myInformation.data">
              <strong>{{item.key}}</strong>
              <span v-html="addTagA(item.value)"/>
            </p>
            <!-- End about-info-->
          </div>
          <div class="col-md-4">
            <h3>{{data.statement.title}}</h3>
            <p v-for="(item, i) in data.statement.data"
              :class="i==0?'text-big':''" v-html="item"/>
          </div>
          <div class="col-md-12 text-center">
            <a href="" class="btn-site btn-download">Download resume</a>
          </div>
        </div>
        <!-- End row-->
        <!-- Start row-->
        <div class="row">
          
        </div>
        <!-- End row-->
      </div>
      <!-- End Container-->
    </section>
    <!--End Section About -->
</template>

<script>

export default {
  name: 'About',
  data () {
    return {
      title: this.$options._componentTag
    }
  },
  methods: {
    addTagA (v) {
      if (/^[http]/.test(v)) {
        return '<a href="' + v + '" target="_blank">' + v + '</a>'
      } else if (v.replace(/[^\d]/g, '').length === 11) {
        return '<a href="tel:' + v + '">' + v + '</a>'
      } else if (!isNaN(v)) {
        return '<div class="tooltip top" role="tooltip">' +
          '  <div class="tooltip-arrow"></div>' +
          '  <div class="tooltip-inner">' + new Date(Number(v)).toDateString() + '</div>' +
          '</div>' + v
      } else {
        return v
      }
    }
  },
  props: ['data']
}
</script>



