<template>
    <!-- Start Section Contact -->
    <section id="navcontact" class="contact-section default-section">
      <!-- Start container-->
      <div class="container">

        <!-- Start row-->
        <div class="row">
          <img src="static/img/contacts.svg" class="section-img" alt="">
          <h2 class="section-title">{{title}}</h2>
        </div>
        <!-- End row-->

        <!-- Start row-->
        <div class="row">

          <div class="col-md-8 col-md-offset-2 text-center">
            <!-- Start social icon-->
            <div class="social-icons">
              <a href="" class="social-item"><i class="uf uf-facebook"></i></a>
              <a href="" class="social-item"><i class="uf uf-twitter"></i></a>
              <a href="" class="social-item"><i class="uf uf-google-plus"></i></a>
              <a href="" class="social-item"><i class="uf uf-instagram"></i></a>
              <a href="" class="social-item"><i class="uf uf-dribbble"></i></a>
            </div>
            <!-- End social icon-->

            <!-- Start Form-->
            <form action="./" method="post">
              <input type="text" name="name" class="input-text" placeholder="Name">
              <input type="text" name="email" class="input-text" placeholder="Email">
              <input type="text" name="subject" class="input-text" placeholder="Subject">
              <textarea name="message" class="input-textarea" placeholder="Message"></textarea>
              <input type="submit" class="btn-site btn-submit" value="Send Message">
            </form>
            <!-- End Form-->

          </div>

        </div>
        <!-- End row-->

      </div>
      <!-- End container-->
    </section>
    <!-- End Section Contact -->
</template>

<script>
export default {
  name: 'Contacts',
  data () {
    return {
      title: this.$options._componentTag
    }
  }
}
</script>



