<template>
  <nav>
    <div class="container">
      <div class="row menu">
        <div class="col-md-12">
          <div id="menu" 
            class="mob-menu" 
            :class="showNav ? 'menu-fixed' : 'default-menu'">
            <ul class="nav" role="tablist">
              <slot name="menu_nav">Here is nav menu</slot>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'Mynav',
  props: ['showNav', 'scrollTo']
}
</script>



