<template>
  <div class="style-graphic">
    <Mynav :showNav="showNav">
      <router-link slot="menu_nav" 
        v-for="(r, i) in menus" 
        :class="{active: scrollPos==i}" 
        :key="r.name"
        :to="'/graphic/'+r.name"
        tag="li">
        <a v-if="i>0">{{r.title}}</a>
        <a v-else
          class="scroll-to-top">
          <i class="uf uf-arrow-up" @click="fixToTop()"/>
        </a>
      </router-link>
    </Mynav>

    <content v-scroll-spy="scrollPos" class="content">
      <Myheader :data="myData.Header">
        <router-link slot="menu_icon" 
          v-for="(r,i) in menus" 
          v-if="i>0"
          :key="r.name"
          :to="'/graphic/'+r.name"
          :class="r.name.toLowerCase()"
          tag="li">
          <a>
            <img :src="'static/img/menu-'+r.name.toLowerCase()+'.svg'">
            <p class="menu-text">{{r.title}}</p>
          </a>
        </router-link>
      </Myheader>
      <About :data="myData.About"/>
      <Resume :data="myData.Resume" :appear="scrollPos"/>
      <Portfolio :data="myData.Portfolio"/>
      <Blog :data="myData.Blog"/>
      <Contacts :data="myData.Contacts"/>
    </content>
    <Myfooter/>
  </div>
</template>

<script>
import Vue from 'vue'
import Mynav from '@/components/Mynav'
import Myheader from '@/components/Myheader'
import About from '@/components/About'
import Resume from '@/components/Resume'
import Portfolio from '@/components/Portfolio'
import Blog from '@/components/Blog'
import Contacts from '@/components/Contacts'
import Myfooter from '@/components/Myfooter'
import $ from 'jquery'
import '@/assets/js/jQplugin/jpreloader'
import scrollSpy from '@/assets/js/scroll-spy'
import myData from '@/assets/js/data'

Vue.use(scrollSpy)

$(() => {
  $('body').jpreLoader()
})

export default {
  name: 'StyleGraphic',
  components: {
    Mynav,
    Myheader,
    About,
    Resume,
    Portfolio,
    Blog,
    Contacts,
    Myfooter
  },
  data () {
    return {
      myData,
      showNav: false,
      menus: [],
      scrollPos: 0
    }
  },
  methods: {
    fixToTop: function () {
      this.scrollPos = 0
      this.$scrollTo(0)
    },
    posChange: name => Object.keys(myData).indexOf(name)
  },
  created () {
    this.menus = Object.keys(myData).map(r => {
      return {
        name: r,
        title: myData[r].title
      }
    })
    this.scrollPos = this.posChange(this.$route.params.part)
  },
  mounted () {
    this.$scrollTo(this.scrollPos)
  },
  watch: {
    scrollPos (to, from) {
      // console.log(to, from)
      if (to === 5 && from === 0) {
        this.showNav = false
      } else {
        this.showNav = to > 0
      }
    },
    '$route' (to, from) {
      this.scrollPos = this.posChange(to.params.part)
      this.$scrollTo(this.scrollPos)
    }
  }
}
</script>



