<template>
    <!-- Start section  portfolio -->
    <section id="navportfolio" class="portfolio-section default-section">
      <!-- Start container-->
      <div class="container">
        <!-- Start row-->
        <div class="row">
          <div class="col-md-12">
            <img src="static/img/portfolio.svg" class="section-img" alt="">
            <h2 class="section-title">{{title}}</h2>
          </div>
        </div>
        <!-- End row-->
        <!-- Start row-->
        <div class="row">
          <div class="col-md-8 col-md-offset-2" id="container-portfolio">
            <!-- Start controls-->
            <div class="controls">
              <!-- Start filter inner-->
              <div class="filter-inner">
                <!-- Start filter btn-site group-->
                <div class="filter-btn-group">
                  <button class="filter" 
                    v-for="(item, i) in worktypes" 
                    :data-filter="i">
                    {{item}}
                  </button>
                </div>
                <!-- End filter btn group-->
              </div>
              <!-- End filter inner-->
            </div>
            <!-- End controls-->

            <!-- Start popup gallery-->
            <div class="popup-gallery grid clearfix">
              <!-- Start mix-->
              <div class="mix" 
                v-for="item in works" 
                :class="item.type">
                <!-- Start popup content-->
                <a class="popup-content popup port-item-cont" 
                  :desc="item.desc" 
                  :href="item.src"
                  :data-title="item.title">
                  <img :src="item.src" alt="">
                  <div class="hover-content">{{item.title}}</div>
                </a>
                <!-- End popup content-->
              </div>
              <!-- End mix-->
            </div>
          </div>
          <!-- End popup gallery-->
          <div class="col-md-12 text-center">
            <a href="#" class="btn-site">Show more</a>
          </div>
        </div>
        <!-- End row-->
      </div>
      <!-- End container-->
    </section>
    <!-- End section portfolio -->
</template>

<script>
import $ from 'jquery'
import mixitup from 'mixitup'
import '@/assets/js/jQplugin/jquery.magnific-popup.min'

let mixer = null

let imgPopUp = () => {
  let popTitle = i => '<div class="modal-box-txt">' +
    '  <h3 class="modal-title">' + i.el.attr('data-title') + '</h3>' +
    '  <p class="modal-box-p">' + i.el.attr('desc') + '</p>' +
    '</div>'
  $('.popup-gallery').magnificPopup({
    type: 'image',
    delegate: '.popup-content',
    closeBtnInside: true,
    gallery: {
      enabled: true, // set to true to enable gallery
      preload: [0, 2], // read about this option in next Lazy-loading section
      navigateByImgClick: true,
      tCounter: '' // '<span class="mfp-counter">%curr%/%total%</span>'
    },
    zoom: {
      enabled: true
    },
    image: {
      tError: '<a href="%url%">The image #%curr%</a> could not be loaded.',
      titleSrc: popTitle
    }
  })

  return mixitup('#container-portfolio', {
    classNames: {
      block: ''
    }
  })
}

export default {
  name: 'Portfolio',
  props: ['data', 'data-appear'],
  data () {
    return {
      title: this.$options._componentTag
    }
  },
  created () {
    let originData = this.$props['data'].works
    let worktypes = {all: ''}
    let works = []
    for (let key in originData) {
      worktypes['.' + key] = originData[key].title
      originData[key].data.map(item => {
        works.push({
          type: key,
          title: item.title,
          src: item.src,
          desc: item.desc
        })
      })
    }
    this.works = works
    this.worktypes = worktypes
  },
  mounted () {
    mixer = imgPopUp()
  },
  destroyed () {
    mixer.destroy()
  }
}
</script>



