<template>
    <!-- Start Section Blog -->
    <section id="navblog" class="blog-section default-section">
      <!-- Start container-->
      <div class="container">

        <!-- Start row-->
        <div class="row">
          <img src="static/img/blog.svg" class="section-img" alt="">
          <h2 class="section-title">{{title}}</h2>
        </div>
        <!-- End row-->

        <!-- Start row-->
        <div class="row blog-lists">

            <div class="col-md-4 blog-list" 
              :class="i&1>0?'':'col-md-offset-2'" 
              v-for="(item, i) in bloglist">
              <!-- <div class="blog-tags">
                <a href="">#photo</a>
                <a href="">#object</a>
                <a href="">#clock</a>
              </div> -->
              <h3 class="blog-title"><a :href="item.link">{{item.title}}</a></h3>
              <a :href="item.link" class="blog-img"><img :src="item.image" alt=""></a>
              <div class="blog-info">
                <div class="blog-info-left">
                  <p class="blog-date">{{item.date}}</p>
                </div>
                <div class="blog-info-right">
                  <p class="blog-author">by
                    <a href="">Nick Edwin </a>
                  </p>
                </div>
              </div>
            </div>
            
        </div>
        <!-- End row-->

        <!-- Start row-->
        <div class="row">
          <div class="col-md-12 text-center">
            <a href="blog.html" class="btn-site">All Posts</a>
          </div>
        </div>
        <!-- End row-->

      </div>
      <!-- End container-->

    </section>
    <!-- End Section Blog -->
</template>

<script>
export default {
  name: 'Blog',
  data () {
    return {
      title: this.$options._componentTag
    }
  },
  props: ['data'],
  created () {
    this.bloglist = this.$props.data.bloglist
  }
}
</script>



